import { Component, OnInit, ElementRef } from '@angular/core';
import { GetdataService } from '../../services/getdata.service';

@Component({
  selector: 'app-cobtansleyfrnt',
  templateUrl: './cobtansleyfrnt.component.html',
  styleUrls: ['./cobtansleyfrnt.component.css']
})
export class CobtansleyfrntComponent implements OnInit {
  
  csvgetdata=[];
  public event_counter:number;
  constructor(private getDataService:GetdataService) { 
    setInterval(() => {
      this.refreshData();
    }, 15000);
  }

  ngOnInit() {
    console.log("data")
    this.event_counter=0;
  this.refreshData();
  }

  refreshData(){
    this.getDataService.getTansleyCSVData(`/csvgets/csvgets`).map(response => response.json()).subscribe(data =>{
      this.csvgetdata = [];
      console.log(data);
      for(let i=this.event_counter; i<data.length; i++)
         {
          this.csvgetdata.push(data[i]) ;
         }

         this.event_counter=this.event_counter+8;

         if(this.event_counter>=data.length)
         {
          this.event_counter=0;
         }
      

    });
  }

}
